import PhotoSwipe from 'photoswipe/dist/photoswipe'
import PhotoSwipeUI_Default from 'photoswipe/dist/photoswipe-ui-default'
import ShowContainer from './showContainer.vue';
import 'photoswipe/dist/default-skin/default-skin.css'
import 'photoswipe/dist/photoswipe.css'

var parseThumbnailElements = async function(items, isFn) {
  // {
  //   src: // 
  //   w: // 
  //   h: // 
  //   msrc: //img
  //   el: figuredom
  // }
  let arr = [];
  for (var i = 0, len = items.length; i < len; i++) {
    await new Promise((resolve, reject) => {
      try {
        let current = '';
        let src = '';
        if (isFn) {
          src = items[i];
        } else {
          current = items[i];
          src = current.getAttribute('big') || current.getAttribute('src');
        }
        let image = document.createElement('img'); 
        image.setAttribute('src', src);
        image.onload = function () {
          arr.push({
            src: src,
            w: image.offsetWidth || image.width || 1024,
            h: image.offsetHeight || image.height || 1024,
            msrc: isFn?src:current.getAttribute('src'),
            el: isFn?image:current,
            title: isFn?'':current.getAttribute('previewTitle')
          })
          resolve()
        }
      } catch (error) {
        reject(error)
      }
    })
  }
  return arr;
};

var openPhotoSwipe = async function(
  index,
  items,
  disableAnimation,
  fromURL,
  isFn
) {
  var pswpElement = document.querySelectorAll(".pswp")[0], //  .pswp 
    gallery,
    options;
  
  // figure
  /* item
    {
      src: // 
      w: // 
      h: // 
      msrc: //img
      el: figuredom
      title: // 
    }
  */
  let itemsTemp= await parseThumbnailElements(items, isFn);
  options = {
    // define gallery index (for URL)
    // galleryUID: items[index].getAttribute("data-pswp-uid"), // .data-pswp-uid 
    history: false, // url
    shareEl: false, // 
    preloaderEl: true, // 
    getThumbBoundsFn: function(index) {
      // See Options -> getThumbBoundsFn section of documentation for more info
      var thumbnail = isFn?itemsTemp[index].el:items[index], // find thumbnail
        pageYScroll =
          window.pageYOffset || document.documentElement.scrollTop, // 
        rect = thumbnail.getBoundingClientRect(); // 
      return { x: rect.left, y: rect.top + pageYScroll, w: rect.width };
    }
  };

  // PhotoSwipe opened from URL
  if (fromURL) { //  fromURL 
    if (options.galleryPIDs) {
      // parse real index when custom PIDs are used
      // http://photoswipe.com/documentation/faq.html#custom-pid-in-url
      for (var j = 0; j < itemsTemp.length; j++) {
        if (itemsTemp[j].pid == index) {
          options.index = j;
          break;
        }
      }
    } else {
      // in URL indexes start from 1
      options.index = parseInt(index, 10) - 1;
    }
  } else {
    options.index = parseInt(index, 10);
  }

  if (disableAnimation) { // disableAnimation
    options.showAnimationDuration = 0;
  }

  // Pass data to PhotoSwipe and initialize it
  gallery = new PhotoSwipe( // 
    pswpElement, //  .pswp
    PhotoSwipeUI_Default, 
    itemsTemp, // itemsTemp
    options // options
  );
  gallery.init(); // 
};

/**
 * imgMark classid
 * @param {*} imgMark 
 */
function initPhotoSwipeFromDOM(imgMark) {
  // parse slide data (url, title, size ...) from DOM elements
  // (children of imgMark)

  // find nearest parent element

  // 
  var onThumbnailsClick = function(e) {
    e = e || window.event;
    e.preventDefault ? e.preventDefault() : (e.returnValue = false);
    var eTarget = e.target || e.srcElement;
    let id = eTarget.getAttribute(imgMark);

    let items = document.querySelectorAll(`[${imgMark}="${id}"]`)
    let index;
    for (let i = 0, len = items.length; i < len; i++) {
      if (items[i] === eTarget) {
        index = i;
      }
    }
    if (index >= 0) {
      // 
      openPhotoSwipe(index, items);
    }
    return false;
  };

  // parse picture index and gallery index from URL (#&pid=1&gid=2)
  // var photoswipeParseHash = function() {
  //   var hash = window.location.hash.substring(1),
  //     params = {};

  //   if (hash.length < 5) {
  //     return params;
  //   }

  //   var vars = hash.split("&");
  //   for (var i = 0; i < vars.length; i++) {
  //     if (!vars[i]) {
  //       continue;
  //     }
  //     var pair = vars[i].split("=");
  //     if (pair.length < 2) {
  //       continue;
  //     }
  //     params[pair[0]] = pair[1];
  //   }

  //   if (params.gid) {
  //     params.gid = parseInt(params.gid, 10);
  //   }

  //   return params;
  // };

  //  imgMark 
  var imgElements = document.querySelectorAll('['+imgMark+']');

  for (var i = 0, l = imgElements.length; i < l; i++) {
    //  data-pswp-uid  + 1
    imgElements[i].setAttribute("data-pswp-uid", i + 1);
    //  onThumbnailsClick
    imgElements[i].onclick = onThumbnailsClick;
  }

  // Parse URL and open gallery if it contains #&pid=3&gid=1
  // hashpidgid
  // var hashData = photoswipeParseHash();
  // if (hashData.pid && hashData.gid) {
  //   openPhotoSwipe(
  //     hashData.pid,
  //     imgElements[hashData.gid - 1],
  //     true,
  //     true
  //   );
  // }
}

export function happypreview (argu) {
  if (argu && typeof argu !== 'object') {
    throw new Error('Parameters can only be arrays and objects')
  }
  let images = []; // 
  let start = 0; // 
  if (Array.isArray(argu)) {
    images = argu;
  } else if (typeof argu === 'object') {
    images = argu.images;
    start = argu.start;
  }
  openPhotoSwipe(start, images, false, false, true);
}

happypreview.install = function (Vue, options = {select: 'previewId'}) {
  // body
  let previewWrapper = document.createElement('div');
  document.body.appendChild(previewWrapper);
  previewWrapper.id = 'preview-wrapper';

  // ShowContainerpreviewWrapper
  var Preview = Vue.extend(ShowContainer);

  //  Profile 
  new Preview().$mount('#preview-wrapper')
  Vue.prototype.$happypreview = happypreview;
  Vue.mixin({
    mounted () {
      initPhotoSwipeFromDOM(options.select)
    }
  })
}
export default happypreview

if (typeof window !== 'undefined' && !window.happypreview) {
  window.happypreview=happypreview;
}